import axios from 'axios';
import ConfirmDialog from '../components/dialogs/ConfirmDialog.vue';
export default {
    name: 'scraping',
    components: {
        ConfirmDialog: ConfirmDialog
    },
    data: function () {
        return {
            running: false,
            timer: null,
            timerstatus: 0,
            dialogWidth: 0,
            tips: '刮削中',
            currentPage: 1,
            totalnum: 10,
            pagesize: 10,
            blur: '',
            sortprop: '',
            sortorder: '',
            editdialog: false,
            rowrecord: [],
            scrapingrecords: [],
            multipleSelection: [],
            selectedoption: -1,
            options: [],
            recordoption: 0,
            scrapingconfig: {},
        };
    },
    created: function () {
        this.setDialogWidth();
    },
    mounted: function () {
        var _this = this;
        this.getconfs();
        this.refresh();
        window.onresize = function () {
            return (function () {
                _this.setDialogWidth();
            })();
        };
    },
    beforeDestroy: function () {
        this.stoptimer();
    },
    methods: {
        getconfs: function () {
            var _this = this;
            var geturl = '/api/scraping/conf/all';
            axios.get(geturl)
                .then(function (response) {
                _this.options = response.data;
                if (_this.selectedoption === -1) {
                    _this.selectedoption = _this.options[0].id;
                }
                _this.changeConf();
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        changeConf: function () {
            var _this = this;
            var config = this.options.find(function (conf) {
                return conf.id == _this.selectedoption;
            });
            if (typeof config === 'undefined') {
                this.selectedoption = this.options[0].id;
                return;
            }
            this.scrapingconfig = config;
        },
        start_all: function () {
            this.running = true;
            this.starttimer();
            axios.post('/api/scraping', this.scrapingconfig)
                .then(function (response) {
                console.log(response);
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        handleSingle: function (index, row) {
            this.running = true;
            this.starttimer();
            row.configid = this.scrapingconfig.id;
            axios.post('/api/scraping/single', row)
                .then(function (response) {
                console.log(response);
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        stop: function () {
            axios.get('/api/stopall')
                .then(function (response) {
                console.log(response);
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        changesort: function (sortProps) {
            this.sortprop = sortProps.prop;
            this.sortorder = sortProps.order;
            this.refresh();
        },
        refresh: function () {
            var _this = this;
            var pageparam = 'page=' + this.currentPage + '&size=' + this.pagesize;
            var sortparam = '&sortprop=' + this.sortprop + '&sortorder=' + this.sortorder;
            var blurparam = '&blur=' + this.blur;
            var geturl = '/api/scraping/record?' + pageparam + sortparam + blurparam;
            axios.get(geturl)
                .then(function (response) {
                _this.timerstatus = 0;
                _this.scrapingrecords = response.data.data;
                _this.currentPage = response.data.page;
                _this.totalnum = response.data.total;
                _this.running = response.data.running;
                if (_this.running) {
                    var tasktotal = response.data.tasktotal;
                    var taskfinished = response.data.taskfinished;
                    var percentage = taskfinished / tasktotal * 100;
                    _this.tips = "刮削进度: " + percentage.toFixed(2) + '%' + " [" + taskfinished + "/" + tasktotal + "]";
                    _this.starttimer();
                }
                else {
                    _this.stoptimer();
                }
            })
                .catch(function (error) {
                console.log(error);
                this.timerstatus = 0;
                this.starttimer();
            }.bind(this));
        },
        starttimer: function () {
            if (this.timerstatus === 0) {
                this.timer = setTimeout(function () { this.refresh(); }.bind(this), 2000);
                this.timerstatus = 1;
            }
        },
        stoptimer: function () {
            clearInterval(this.timer);
            this.timer = null;
            this.timerstatus = 0;
        },
        toggleSelection: function (rows) {
            var _this = this;
            if (rows) {
                rows.forEach(function (row) {
                    _this.$refs.multipleTable.toggleRowSelection(row);
                });
            }
            else {
                this.$refs.multipleTable.clearSelection();
            }
        },
        handleSelectionChange: function (val) {
            this.multipleSelection = val;
        },
        handleSearch: function () {
            this.refresh();
        },
        delrecords: function () {
            var _this = this;
            this.$refs.confirm
                .show()
                .then(function () {
                // alert(this.$refs.confirm.subcheck)
                var delsrc = _this.$refs.confirm.subcheck;
                var ids = new Array();
                _this.multipleSelection.forEach(function (item) {
                    ids.push(item.id);
                });
                var del = { 'ids': ids, 'delsrc': delsrc };
                axios.delete('/api/scraping/record', { data: del })
                    .then(function () {
                    _this.$message({
                        showClose: true,
                        duration: 2000,
                        message: '清理成功',
                        type: 'success'
                    });
                    _this.refresh();
                })
                    .catch(function (error) {
                    console.log(error);
                });
            })
                .catch(function () {
                console.log("You rejected");
            });
        },
        handleEdit: function (index, row) {
            this.rowrecord = row;
            this.editdialog = true;
            if (this.rowrecord.locked) {
                this.recordoption = 1;
            }
            else if (this.rowrecord.ignroed) {
                this.recordoption = 2;
            }
            else {
                this.recordoption = 0;
            }
        },
        cancelDeadtime: function () {
            this.rowrecord.deadtime = '';
        },
        dialogexit: function () {
            this.editdialog = false;
            this.refresh();
        },
        dialogupdate: function () {
            var _this = this;
            this.editdialog = false;
            if (this.recordoption == 1) {
                this.rowrecord.ignored = false;
                this.rowrecord.locked = true;
            }
            else if (this.recordoption == 2) {
                this.rowrecord.ignored = true;
                this.rowrecord.locked = false;
            }
            else {
                this.rowrecord.ignored = false;
                this.rowrecord.locked = false;
            }
            axios.put('/api/scraping/record', this.rowrecord)
                .then(function (response) {
                _this.refresh();
                console.log(response);
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        handleCurrentChange: function (num) {
            this.currentPage = num;
            this.refresh();
        },
        handleSizeChange: function (val) {
            this.pagesize = val;
            this.refresh();
        },
        setDialogWidth: function () {
            // console.log(document.body.clientWidth)
            var val = document.body.clientWidth;
            var def = 700;
            if (val < def) {
                this.dialogWidth = '90%';
            }
            else {
                this.dialogWidth = def + 'px';
            }
        },
    }
};
