import axios from 'axios';
export default {
    name: 'automation',
    data: function () {
        return {
            config: {},
            records: [],
            troptions: [],
            checkedtr: [],
            scoptions: [],
            checkedsc: []
        };
    },
    mounted: function () {
        var _this = this;
        this.getTransferConf();
        this.getScrapingConf();
        this.getTasks();
        this.timer = setInterval(this.getTasks, 1500);
        axios.get('/api/auto/conf')
            .then(function (response) {
            _this.config = response.data;
            var trs = _this.config.transferconfs.split(';');
            for (var i = 0; i < trs.length; i++) {
                if (trs[i] != '') {
                    _this.checkedtr.push(Number(trs[i]));
                }
            }
            var scs = _this.config.scrapingconfs.split(';');
            for (var i = 0; i < scs.length; i++) {
                if (scs[i] != '') {
                    _this.checkedsc.push(Number(scs[i]));
                }
            }
        })
            .catch(function (error) {
            console.log(error);
        });
    },
    beforeDestroy: function () {
        clearInterval(this.timer);
    },
    methods: {
        getTransferConf: function () {
            var _this = this;
            var geturl = '/api/transfer/conf/all';
            axios.get(geturl)
                .then(function (response) {
                _this.troptions = response.data;
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        handleTrChange: function (value) {
            var selected = [];
            for (var i = 0; i < value.length; i++) {
                if (!selected.includes(value[i])) {
                    selected.push(value[i]);
                }
            }
            this.config.transferconfs = selected.join(';');
        },
        getScrapingConf: function () {
            var _this = this;
            var geturl = '/api/scraping/conf/all';
            axios.get(geturl)
                .then(function (response) {
                _this.scoptions = response.data;
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        handleScChange: function (value) {
            var selected = [];
            for (var i = 0; i < value.length; i++) {
                if (!selected.includes(value[i])) {
                    selected.push(value[i]);
                }
            }
            this.config.scrapingconfs = selected.join(';');
        },
        onSubmit: function () {
            var _this = this;
            axios.put('/api/auto/conf', this.config)
                .then(function () {
                _this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '保存成功',
                    type: 'success'
                });
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        getTasks: function () {
            var _this = this;
            axios.get('/api/auto/task')
                .then(function (response) {
                _this.records = response.data;
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        clean: function () {
            var _this = this;
            axios.delete('/api/auto/task')
                .then(function () {
                _this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '清理完成',
                    type: 'success'
                });
            })
                .catch(function (error) {
                console.log(error);
            });
        },
    }
};
