import axios from 'axios';
export default {
    name: 'options',
    data: function () {
        return {
            loglevel: 20,
            libcurrent: "0.1.0",
            liblatest: "0.1.0",
            loginfo: '',
            lastinfo: '',
            logtimer: null,
            localConf: {}
        };
    },
    mounted: function () {
        var _this = this;
        this.getconfig();
        axios.get('/api/options/loglevel')
            .then(function (response) {
            _this.loglevel = response.data.loglevel;
        })
            .catch(function (error) {
            console.log(error);
        });
        // TODO: fix url
        var ws = new WebSocket("ws://".concat(location.host, "/ws/logstream?tail=1"));
        ws.onmessage = function (event) {
            _this.lastinfo = event.data;
            _this.loginfo = _this.loginfo + _this.lastinfo;
            _this.$refs.output.textContent = _this.loginfo;
            _this.$refs.output.scrollTop = _this.$refs.output.scrollHeight;
        };
        ws.onerror = function () {
            console.log("ws close");
            ws.close();
        };
    },
    methods: {
        updateloglvl: function () {
            var _this = this;
            var data = { loglevel: this.loglevel };
            axios.put('/api/options/loglevel', data)
                .then(function () {
                _this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '保存成功',
                    type: 'success'
                });
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        getconfig: function () {
            var _this = this;
            axios.get('/api/options/config')
                .then(function (response) {
                _this.localConf = response.data;
            });
        },
        updateconfig: function () {
            var _this = this;
            axios.put('/api/options/config', this.localConf)
                .then(function () {
                _this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '保存成功',
                    type: 'success'
                });
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        confValueChange: function (v) {
            console.log(v);
            this.updateconfig();
        },
        checklibver: function () {
            var _this = this;
            axios.get('/api/options/check/lib')
                .then(function (response) {
                _this.libcurrent = response.data.installed;
                _this.liblatest = response.data.latest;
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        updatelib: function () {
            axios.put('/api/options/check/lib')
                .then(function () {
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        cleandata: function () {
            var _this = this;
            axios.get('/api/options/cleanrecord')
                .then(function (response) {
                console.log(response);
                _this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '清理成功',
                    type: 'success'
                });
            })
                .catch(function (error) {
                console.log(error);
            });
        },
    }
};
