import axios from 'axios';
import FileBrowserDialog from '../components/dialogs/FileBrowserDialog.vue';
export default {
    name: 'scrapingconf',
    components: {
        FileBrowserDialog: FileBrowserDialog,
    },
    data: function () {
        return {
            isDialogVisible: false,
            openDialogID: 1,
            folderPath: '',
            mode: 1,
            scrapingconfig: {},
            selectedoption: -1,
            options: [],
        };
    },
    mounted: function () {
        this.getconfs();
    },
    methods: {
        getconfs: function () {
            var _this = this;
            var geturl = '/api/scraping/conf/all';
            axios.get(geturl)
                .then(function (response) {
                _this.options = response.data;
                if (_this.selectedoption === -1) {
                    _this.selectedoption = _this.options[0].id;
                }
                _this.changeConf();
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        changeConf: function () {
            var _this = this;
            var config = this.options.find(function (conf) {
                return conf.id == _this.selectedoption;
            });
            if (typeof config === 'undefined') {
                this.selectedoption = this.options[0].id;
                this.scrapingconfig = this.options[0];
                return;
            }
            this.scrapingconfig = config;
            if (this.scrapingconfig.main_mode === 3) {
                this.mode = 4;
            }
            else {
                if (this.scrapingconfig.link_type === 1) {
                    this.mode = 1;
                }
                else if (this.scrapingconfig.link_type === 2) {
                    this.mode = 2;
                }
                else {
                    this.mode = 3;
                }
            }
        },
        addconf: function () {
            var _this = this;
            if (this.mode === 1) {
                this.scrapingconfig.link_type = 1;
                this.scrapingconfig.main_mode = 1;
            }
            else if (this.mode === 2) {
                this.scrapingconfig.link_type = 2;
                this.scrapingconfig.main_mode = 1;
            }
            else if (this.mode === 3) {
                this.scrapingconfig.link_type = 0;
                this.scrapingconfig.main_mode = 1;
            }
            else if (this.mode === 4) {
                this.scrapingconfig.main_mode = 3;
            }
            axios.post('/api/scraping/conf', this.scrapingconfig)
                .then(function (res) {
                _this.scrapingconfig = res.data;
                _this.selectedoption = _this.scrapingconfig.id;
                _this.getconfs();
                _this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '新增成功',
                    type: 'success'
                });
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        updateconf: function () {
            var _this = this;
            if (this.mode === 1) {
                this.scrapingconfig.link_type = 1;
                this.scrapingconfig.main_mode = 1;
            }
            else if (this.mode === 2) {
                this.scrapingconfig.link_type = 2;
                this.scrapingconfig.main_mode = 1;
            }
            else if (this.mode === 3) {
                this.scrapingconfig.link_type = 0;
                this.scrapingconfig.main_mode = 1;
            }
            else if (this.mode === 4) {
                this.scrapingconfig.main_mode = 3;
            }
            axios.put('/api/scraping/conf', this.scrapingconfig)
                .then(function () {
                _this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '保存成功',
                    type: 'success'
                });
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        deleteconf: function () {
            var _this = this;
            if (this.options.length < 2) {
                this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '仅剩一份配置无法删除',
                    type: 'success'
                });
                return;
            }
            axios.delete('/api/scraping/conf/' + this.scrapingconfig.id)
                .then(function () {
                _this.getconfs();
                _this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '删除成功',
                    type: 'success'
                });
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        showSourceDialog: function () {
            this.openDialogID = 1;
            this.isDialogVisible = true;
        },
        showOutputDialog: function () {
            this.openDialogID = 2;
            this.isDialogVisible = true;
        },
        showFailedDialog: function () {
            this.openDialogID = 3;
            this.isDialogVisible = true;
        },
        closeDialog: function () {
            this.isDialogVisible = false;
            if (this.openDialogID === 1) {
                this.scrapingconfig.scraping_folder = this.folderPath;
            }
            else if (this.openDialogID === 2) {
                this.scrapingconfig.success_folder = this.folderPath;
            }
            else if (this.openDialogID === 3) {
                this.scrapingconfig.failed_folder = this.folderPath;
            }
        }
    }
};
