import axios from 'axios';
import FileBrowserDialog from '../components/dialogs/FileBrowserDialog.vue';
import ConfirmDialog from '../components/dialogs/ConfirmDialog.vue';
export default {
    name: 'transfer',
    components: {
        FileBrowserDialog: FileBrowserDialog,
        ConfirmDialog: ConfirmDialog,
    },
    data: function () {
        return {
            running: false,
            timer: null,
            timerstatus: 0,
            tips: '转移中',
            isDialogVisible: false,
            openDialogID: 1,
            folderPath: '',
            currentPage: 1,
            totalnum: 10,
            pagesize: 10,
            blur: '',
            sortprop: '',
            sortorder: '',
            transferdata: [],
            transconfig: {},
            selectedoption: -1,
            options: [],
            multipleSelection: [],
            dialogVisible: false,
            editdialog: false,
            rowrecord: [],
            recordoption: 0,
            renameAllTop: false,
            renameAllSub: false,
        };
    },
    created: function () {
        this.getconfs();
        this.setDialogWidth();
    },
    mounted: function () {
        var _this = this;
        this.refresh();
        window.onresize = function () {
            return (function () {
                _this.setDialogWidth();
            })();
        };
    },
    beforeDestroy: function () {
        this.stoptimer();
    },
    methods: {
        onSubmit: function () {
            this.running = true;
            this.starttimer();
            axios.post('/api/transfer', this.transconfig)
                .catch(function (error) {
                console.log(error);
            });
        },
        stop: function () {
            axios.get('/api/stopall')
                .then(function (response) {
                console.log(response);
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        changesort: function (sortProps) {
            this.sortprop = sortProps.prop;
            this.sortorder = sortProps.order;
            this.refresh();
        },
        refresh: function () {
            var _this = this;
            var pageparam = 'page=' + this.currentPage + '&size=' + this.pagesize;
            var sortparam = '&sortprop=' + this.sortprop + '&sortorder=' + this.sortorder;
            var blurparam = '&blur=' + this.blur;
            var geturl = '/api/transfer/record?' + pageparam + sortparam + blurparam;
            axios.get(geturl)
                .then(function (response) {
                _this.timerstatus = 0;
                _this.transferdata = response.data.data;
                _this.currentPage = response.data.page;
                _this.totalnum = response.data.total;
                _this.running = response.data.running;
                if (_this.running) {
                    var tasktotal = response.data.tasktotal;
                    var taskfinished = response.data.taskfinished;
                    var percentage = taskfinished / tasktotal * 100;
                    _this.tips = "转移进度: " + percentage.toFixed(2) + '%' + " [" + taskfinished + "/" + tasktotal + "]";
                    console.log(_this.tips);
                    _this.starttimer();
                }
                else {
                    _this.stoptimer();
                }
            })
                .catch(function (error) {
                console.log(error);
                this.timerstatus = 0;
                this.starttimer();
            }.bind(this));
        },
        starttimer: function () {
            if (this.timerstatus === 0) {
                this.timer = setTimeout(function () { this.refresh(); }.bind(this), 2000);
                this.timerstatus = 1;
            }
        },
        stoptimer: function () {
            clearInterval(this.timer);
            this.timer = null;
            this.timerstatus = 0;
        },
        delrecords: function () {
            var _this = this;
            this.$refs.confirm
                .show()
                .then(function () {
                var delsrc = _this.$refs.confirm.subcheck;
                var ids = new Array();
                _this.multipleSelection.forEach(function (item) {
                    ids.push(item.id);
                });
                var del = { 'ids': ids, 'delsrc': delsrc };
                axios.delete('/api/transfer/record', { data: del })
                    .then(function () {
                    _this.$message({
                        showClose: true,
                        duration: 2000,
                        message: '清理成功',
                        type: 'success'
                    });
                    _this.refresh();
                })
                    .catch(function (error) {
                    console.log(error);
                });
            })
                .catch(function () {
                console.log("You rejected");
            });
        },
        editfolders: function () {
            console.log('te');
        },
        editfoldersEnable: function () {
            return false;
        },
        handleCurrentChange: function (num) {
            this.currentPage = num;
            this.refresh();
        },
        handleSizeChange: function (val) {
            this.pagesize = val;
            this.refresh();
        },
        getconfs: function () {
            var _this = this;
            var geturl = '/api/transfer/conf/all';
            axios.get(geturl)
                .then(function (response) {
                _this.options = response.data;
                if (_this.selectedoption === -1) {
                    _this.selectedoption = _this.options[0].id;
                }
                _this.changeConf();
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        changeConf: function () {
            var _this = this;
            var config = this.options.find(function (conf) {
                return conf.id == _this.selectedoption;
            });
            if (typeof config === 'undefined') {
                this.selectedoption = this.options[0].id;
                this.transconfig = this.options[0];
                return;
            }
            this.transconfig = config;
        },
        addconf: function () {
            var _this = this;
            axios.post('/api/transfer/conf', this.transconfig)
                .then(function (res) {
                _this.transconfig = res.data;
                _this.selectedoption = _this.transconfig.id;
                _this.getconfs();
                _this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '新增成功',
                    type: 'success'
                });
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        updateconf: function () {
            var _this = this;
            axios.put('/api/transfer/conf', this.transconfig)
                .then(function (res) {
                _this.transconfig = res.data;
                _this.getconfs();
                _this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '保存成功',
                    type: 'success'
                });
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        deleteconf: function () {
            var _this = this;
            if (this.options.length < 2) {
                this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '仅剩一份配置无法删除',
                    type: 'success'
                });
                return;
            }
            axios.delete('/api/transfer/conf/' + this.transconfig.id)
                .then(function () {
                _this.getconfs();
                _this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '删除成功',
                    type: 'success'
                });
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        showSourceDialog: function () {
            this.openDialogID = 1;
            this.isDialogVisible = true;
        },
        showOutputDialog: function () {
            this.openDialogID = 2;
            this.isDialogVisible = true;
        },
        closeDialog: function () {
            this.isDialogVisible = false;
            if (this.openDialogID === 1) {
                this.transconfig.source_folder = this.folderPath;
            }
            else if (this.openDialogID === 2) {
                this.transconfig.output_folder = this.folderPath;
            }
        },
        toggleSelection: function (rows) {
            var _this = this;
            if (rows) {
                rows.forEach(function (row) {
                    _this.$refs.multipleTable.toggleRowSelection(row);
                });
            }
            else {
                this.$refs.multipleTable.clearSelection();
            }
        },
        handleSelectionChange: function (val) {
            this.multipleSelection = val;
        },
        handleEdit: function (index, row) {
            this.rowrecord = row;
            this.editdialog = true;
            if (this.rowrecord.locked) {
                this.recordoption = 1;
            }
            else if (this.rowrecord.ignroed) {
                this.recordoption = 2;
            }
            else {
                this.recordoption = 0;
            }
            this.renameAllTop = false;
            this.renameAllSub = false;
        },
        cancelDeadtime: function () {
            this.rowrecord.deadtime = '';
        },
        dialogexit: function () {
            this.editdialog = false;
            this.refresh();
        },
        dialogupdate: function () {
            var _this = this;
            this.editdialog = false;
            if (this.recordoption == 1) {
                this.rowrecord.ignored = false;
                this.rowrecord.locked = true;
            }
            else if (this.recordoption == 2) {
                this.rowrecord.ignored = true;
                this.rowrecord.locked = false;
            }
            else {
                this.rowrecord.ignored = false;
                this.rowrecord.locked = false;
            }
            this.rowrecord.renameAllTop = this.renameAllTop;
            this.rowrecord.renameAllSub = this.renameAllSub;
            axios.put('/api/transfer/record', this.rowrecord)
                .then(function (response) {
                _this.refresh();
                console.log(response);
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        setDialogWidth: function () {
            var val = document.body.clientWidth;
            var def = 700;
            if (val < def) {
                this.dialogWidth = '90%';
            }
            else {
                this.dialogWidth = def + 'px';
            }
        },
        handleSearch: function () {
            this.refresh();
        }
    }
};
