import axios from 'axios';
import FileBrowserDialog from '../components/dialogs/FileBrowserDialog.vue';
export default {
    name: 'rename',
    components: {
        FileBrowserDialog: FileBrowserDialog,
    },
    data: function () {
        return {
            isDialogVisible: false,
            prenames: [],
            renametype: 0,
            renameconf: {
                source_folder: '',
                reg: '',
                prefix: '',
                ext_type: '',
                base: '',
                newfix: ''
            }
        };
    },
    methods: {
        preview: function () {
            var _this = this;
            axios.post('/api/previewrename', this.renameconf)
                .then(function (response) {
                if (response.data.length === 0) {
                    _this.$message({
                        showClose: true,
                        duration: 2000,
                        message: '未发现可改名文件',
                        type: 'warning'
                    });
                }
                else {
                    _this.prenames = response.data;
                }
            })
                .catch(function () {
                _this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '异常! 请查看日志或检查路径是否存在',
                    type: 'error'
                });
            });
        },
        apply: function () {
            var _this = this;
            this.fixnames = [],
                this.prenames = [],
                axios.post('/api/renamebyreg', this.renameconf)
                    .then(function (response) {
                    if (response.data.length === 0) {
                        _this.$message({
                            showClose: true,
                            duration: 2000,
                            message: '未发现可改名文件',
                            type: 'success'
                        });
                    }
                    else {
                        _this.prenames = response.data;
                        _this.$message({
                            showClose: true,
                            duration: 2000,
                            message: '重命名成功',
                            type: 'success'
                        });
                    }
                })
                    .catch(function () {
                    _this.$message({
                        showClose: true,
                        duration: 2000,
                        message: '异常! 请查看日志或检查路径是否存在',
                        type: 'error'
                    });
                });
        },
        replace: function () {
            var _this = this;
            axios.post('/api/renamebyrep', this.renameconf)
                .then(function (response) {
                console.log(response);
                _this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '重命名成功',
                    type: 'success'
                });
            })
                .catch(function () {
                _this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '异常! 请查看日志或检查路径是否存在',
                    type: 'error'
                });
            });
        },
        showDialog: function () {
            this.isDialogVisible = true;
        },
        closeDialog: function () {
            this.isDialogVisible = false;
        }
    }
};
